import React, { Component } from 'react';

class BasicInfo extends Component {
  render() {
    return (
        <aside className="info aside section">
            <div className="section-inner">
                <h2 className="heading sr-only">Basic Information</h2>
                <div className="content">
                    <ul className="list-unstyled">
                        <li><i className="fa fa-map-marker"></i><span className="sr-only">Location:</span>Orem, UT</li>
                        <li><i className="fa fa-envelope-o"></i><span className="sr-only">Email:</span><a href="mailto:brandonkshepherd@gmail.com">brandonkshepherd@gmail.com</a></li>
                        <li><i className="fa fa-phone"></i><span className="sr-only">Phone:</span>801-430-8667</li>
                        <li><i className="fa fa-link"></i><span className="sr-only">Website:</span><a href="#">https://branshep.github.io</a></li>
                    </ul>
                </div>
            </div>               
        </aside>
    );
  }
}

export default BasicInfo;


