import React, { Component } from 'react';
import ProjectImage from './ProjectImage';
import Modal from 'react-modal';

class ProjectImages extends Component {
  state = {
    modalVisible: false
  }

  setModalVisible() {
      this.setState({ modalVisible: true });
  }

  renderImages(projectImagesArray) {
    return this.props.projectImagesArray.map((image, index) => {
        return <ProjectImage setModalVisible={this.setModalVisible.bind(this)} imageFileName={image} />
    });
  }

  renderModalImages(projectImagesArray) {
    return this.props.projectImagesArray.map((image, index) => {
        if (index === 0) {
            return (
                <div className="item active" style={modalStyles.imageSize}> 
                    <img className="carousel-img" src={`assets/images/${image}`} alt={`item${index}`} />
                </div>
            );
        }
        return (
            <div className="item" style={modalStyles.imageSize}> 
                <img className="carousel-img" src={`assets/images/${image}`} alt={`item${index}`} />
            </div>
        );
    });
  }

  render() {
    return (
        <div className="image-bar">
            {this.renderImages(this.props.projectImagesArray)}

            
            <Modal
              isOpen={this.state.modalVisible}
              contentLabel="Modal"
              style={modalStyles}
              shouldCloseOnOverlayClick={true}
              onClick={() => console.log("yyo youy oy")}
            >   
                <div id="myGallery" className="carousel slide" data-interval="false">
                    <div className="carousel-inner">
                        {this.renderModalImages()}
                    </div>
                            
                    <a 
                      className="left carousel-control" href="#myGallery" 
                      role="button" data-slide="prev"
                    > 
                        <span className="glyphicon glyphicon-chevron-left"></span>
                    </a>

                    <a 
                      className="right carousel-control" 
                      href="#myGallery" 
                      role="button" 
                      data-slide="next"
                    >
                        <span className="glyphicon glyphicon-chevron-right"></span>
                    </a>                    
                </div>

                <a onClick={() => this.setState({ modalVisible: false })}>
                    <p style={modalStyles.closeButton}> close </p>
                </a>

            </Modal>
        </div>
    );
  }
}

const modalStyles = {
  overlay: {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0,0,0,0.5)'
  },
  content: {
    margin: 'auto',
    outline: 'none',
    top: 0,
    left: 0,
    bottom: 0,
    right: 0,
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    overflow: 'hidden',
    border: 'none',
    padding: 0,
  },
  closeButton: {
    position: 'absolute',
    top: '30px',
    right: '30px',
    fontSize: '50px',
    color: '#ddd',
    cursor: 'pointer'
  },
  imageSize: {
    width: '380px'
  }
}

export default ProjectImages;


