import React, { Component } from 'react';
import Skill from './Skill';

class Skills extends Component {
  render() {
    return (
        <aside className="skills aside section">
            <div className="section-inner">
                <h2 className="heading">Skills</h2>
                <div className="content">
                    <p className="intro">
                        I spend most of my time programming using React and React Native. I learn a little more about javascript everyday. Looking to expand my skillset to include C# and Java.
                    </p>
                            
                    <div className="skillset">
                        <Skill 
                            title={'React, React Native, Redux'}
                            level={'Expert'}
                            percentage={'96%'}
                        />

                        <Skill 
                            title={'Javascript, jQuery'}
                            level={'Expert'}
                            percentage={'96%'}
                        />

                        <Skill 
                            title={'HTML5, CSS3, SASS, LESS'}
                            level={'Expert'}
                            percentage={'96%'}
                        />

                        <Skill 
                            title={'Angular 1'}
                            level={'Intermediate'}
                            percentage={'70%'}
                        />

                        <Skill 
                            title={'Node, Express'}
                            level={'Intermediate'}
                            percentage={'70%'}
                        />

                        <Skill 
                            title={'Postgres'}
                            level={'Intermediate'}
                            percentage={'70%'}
                        />

                        <Skill 
                            title={'Photoshop'}
                            level={'Intermediate'}
                            percentage={'70%'}
                        />              
                    </div>
                    
                    <div style={styles.techStack}>
                        <i className="devicon-react-plain colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-angularjs-plain colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-postgresql-plain colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-nodejs-plain colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-express-original colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-css3-plain colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-html5-plain colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-sass-plain colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-less-plain-wordmark colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-jquery-plain colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-babel-plain colored" style={{ fontSize: 70, margin: 5 }}></i>
                        <i className="devicon-webpack-plain colored" style={{ fontSize: 70, margin: 5 }}></i>
                    </div>               
                </div>
            </div>              
        </aside>
    );
  }
}

const styles = {
    techStack: {
        width: '100%',
        display: 'flex',
        flexDirection: 'row',
        flexWrap: 'wrap',
        justifyContent: 'space-between'
    }
}

export default Skills;


